<template>
    <el-form class="login" :model="registerForm" :rules="rules" ref="registerForm">
        <div class="container">
            <div class="row">
                <el-form-item class="col-sm-12 col-lg-12" :error="nameErr" label="用户名" prop="user_name">
                    <el-input type="text" v-model="registerForm.user_name"></el-input>
                </el-form-item>
                <el-form-item class="col-sm-12 col-lg-12" :error="emailErr" label="邮箱" prop="user_email">
                    <el-input type="email" v-model="registerForm.user_email"></el-input>
                </el-form-item>
                <el-form-item class="col-sm-12 col-lg-12" label="密码" prop="user_password">
                    <el-input type="password" v-model="registerForm.user_password" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item class="col-sm-12 col-lg-12" label="确认密码" prop="checkpass">
                    <el-input type="password" v-model="registerForm.checkpass" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item class="col-sm-12 col-lg-12" label="验证码" prop="verification">
                    <img class="verifications" :src="imgDataURL" @click="changeVerification" alt="">
                    <el-input type="text" v-model="registerForm.verification" autocomplete="off"></el-input>
                </el-form-item>
            </div>
        </div>
                <el-form-item>
                    <el-button style="width:100%" type="primary" @click="submitForm('registerForm')">注册</el-button>
                </el-form-item>
  </el-form>
</template>

<script>
import verification from 'verification-code'

export default {
  data () {
    var Pass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.registerForm.checkPass !== '') {
          this.$refs.registerForm.validateField('checkPass')
        }
        callback()
      }
    }
    var ValidatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.registerForm.user_password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    var ValidateEmail = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入邮箱'))
      } else {
        var reg = /^\w+@\w+(\.[a-zA-Z]{2,3}){1,2}$/
        if (reg.test(value) === false) {
          callback(new Error('邮箱格式不正确，例如web@baidu.com'))
        } else {
          callback()
        }
      }
    }
    var Verification = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入验证码'))
      } else if (value !== this.code) {
        callback(new Error('验证码不正确!'))
      } else {
        callback()
      }
    }
    return {
      imgDataURL: '',
      code: '',
      nameErr: '',
      emailErr: '',
      registerForm: {
        user_id: 0,
        user_name: '',
        user_email: '',
        user_password: '',
        checkpass: '',
        verification: ''
      },
      rules: {
        user_name: [
          {required: true, message: '请输入用户名', trigger: 'blur'},
          {max: 6, min: 3, message: '用户名长度应在3-6位', trigger: 'blur'}
        ],
        user_email: [
          { required: true, validator: ValidateEmail, trigger: 'blur' }
        ],
        user_password: [
          { required: true, validator: Pass, trigger: 'blur' }
        ],
        checkpass: [
          { required: true, validator: ValidatePass, trigger: 'blur' }
        ],
        verification: [
          { required: true, validator: Verification, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    changeVerification () {
      var result = verification.create()
      this.code = result.code
      this.imgDataURL = result.dataURL
      console.log(this.code, this.imgDataURL)
    },
    submitForm (formName) {
      
    }
  },
  mounted () {
    this.changeVerification()
  }
}
</script>

<style lang="stylus" scoped>
.verifications
    width 90px
    height 30px
    display block
    background #eee
.login
  width 340px
  margin 0 auto
</style>



