<template>
<div class="row">
    <div id="swiper"  class="swiper-container banner">
                <div class="swiper-wrapper">
                    <div class="swiper-slide">
                        <div id="home" class="col-sm-12 text-center">
                            <div class="background">
                                <h2>欢迎品尝pizza!</h2>
                                <h3>这里有你非常喜欢的pizza！</h3>
                                <button class="btn btn-success">
                                    <router-link to="">
                                        点菜
                                    </router-link>
                                </button>
                            </div>    
                        </div>
                    </div>
                    <div class="swiper-slide">
                        <div id="home" class="col-sm-12 text-center">
                            <div class="background">
                                <h2>欢迎登陆!</h2>
                                <h3>这里有你非常喜欢的pizza！</h3>
                                <button class="btn btn-success">
                                    <router-link to="/login">
                                        登陆
                                    </router-link>
                                </button>
                            </div>    
                        </div>
                    </div>
                    <div class="swiper-slide">
                        <div id="home" class="col-sm-12 text-center">
                            <div class="background">
                                <h2>欢迎注册!</h2>
                                <h3>这里有你非常喜欢的pizza！</h3>
                                <button class="btn btn-success">
                                    <router-link to="/register">
                                        注册
                                    </router-link>
                                </button>
                            </div>    
                        </div>
                    </div>
                </div>
                <div class="swiper-pagination"></div>
          </div>
</div>
</template>

<script>
import Swiper from 'swiper'

export default {
    mounted () {
    new Swiper('#swiper', { // eslint-disable-line no-new
      loop: true,
      speed: 500,
      autoplay: true,
      observer: true, // 修改swiper自己或子元素时，自动初始化swiper
      observeParents: true, // 修改swiper的父元素时，自动初始化swiper
      pagination: {
        el: '.swiper-pagination',
      }
    })
    }
}
</script>

<style>
#home{
    background: url('../../static/pisa.jpg') no-repeat center;
     padding: 10%
}
h1,h2{
    margin: 6%
}
.background{
    background: #eee;
    opacity: 0.8;
    max-width: 70vw;
    margin: 0 auto;
    padding: 20px 0;
}
.btn a{
    color: #eee;
}
</style>



