<template>
  <el-form class="login" :model="loginForm" :rules="rules" ref="loginForm">
      <div class="container">
            <div class="row">
      <el-form-item class="col-sm-12 col-lg-12" :error="passErr" label="邮箱" prop="user_email">
          <el-input v-model="loginForm.user_email"></el-input>
      </el-form-item>
      <el-form-item class="col-sm-12 col-lg-12" :error="passErr" label="密码" prop="user_password">
          <el-input type="password" v-model="loginForm.user_password"></el-input>
      </el-form-item>
      </div>
      </div>

      <el-form-item>
          <el-button style="width:100%" type="primary" @click="login">登录</el-button>
      </el-form-item>
  </el-form>
</template>

<script>
export default {
  data () {
    var validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入邮箱'))
      } else {
        var reg = /\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/
        if (reg.test(value) === false) {
          callback(new Error('邮箱格式不正确'))
        } else {
          callback()
        }
      }
    }
    return {
      passErr: '',
      loginForm: {
        user_email: '',
        user_password: ''
      },
      rules: {
        user_email: [
          { required: true, validator: validatePhone, trigger: 'blur' }
        ],
        user_password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    login () {
      
    }
  }
}
</script>

<style lang="stylus" scoped>
.login
  width 340px
  margin 0 auto
</style>



